<template>
	<div>
		参数：
		<div>
			资源组序号：
			<select v-model="select" @change="getlog()">
				<option value='0'>请选择</option>
				<option :value="item.id" v-for="(item,index) in select" :key="index">{{item.val}}</option>
			</select>
			<select name="selected" id="" v-model="selected" @change="getTypeSelected">
				<option value=''>请选择</option>
				<option :value="types.id" v-for="types,index in typeList" :key="index">{{types.name}}</option>
			</select>
		</div>
	</div>
	ExCode：<input type="text" name="excode" id="excode" v-model="code">
	<div>
		<button @click="exCode(1)">转34进制</button>
		<button @click="exCode(2)">转10进制</button>
		<button @click="exCode(4)">转换</button>
	</div>
	<div>
		<button @click="exCode(3)">生成</button>
		<button @click="exCode(5)">生成并乱序</button>
	</div>
	Output：<input type="text" v-model="output" placeholder="转换输出内容">
	<el-divider />
	<div class="tempoutput">
		兑换码：<input type="text" name="excode" id="codeinput" v-model="excode">
		<button type="button" @click="exdialog = true">领取</button>
		<button type="button" @click="exCode(7)">解析</button>
	</div>
	<el-divider />
	<NavBar />

	<!-- 对话框 -->
	<el-dialog v-model="exdialog" title="恭喜获得" width="360px" align-center>
		<div class="temp_listdiv">
			<!-- <ul class="temp_listul"> list.length-->
			<div class="temp_listul" v-for="(item,index) in typeList" :key="index">{{item.name}}</div>
			<!-- </ul> -->
		</div>
		<template #footer>
			<span class="dialog-footer">
				<!-- <el-button @click="exdialog = false">取消</el-button> -->
				<el-button type="primary" @click="exdialog = false">领取</el-button>
			</span>
		</template>
	</el-dialog>
	<!-- 对话框结束 -->
</template>

<script>
	console.log('Script执行成功！');
	import NavBar from '@/components/NavBar.vue';
	import {ref} from 'vue';
	export default {
		name: 'ExCode',
		components: {
			NavBar
		},
		data() {
			return {
				code: '',
				output: '',
				select: [],
				typeList: [{
						id: 1,
						name: '违规类型'
					},
					{
						id: 2,
						name: '无人值守'
					},
					{
						id: 3,
						name: '蒙头睡觉'
					},
				],
				selected: '',
				exdialog: false,
				excode: ref('')
			}
		},
		setup() {
			console.clear();

		},
		methods: {
			getlog() {
				if (!this.select) {
					this.$set(this.select, this.select.id, this.select.val)
					console.log(this.select);
				}
			},
			getTypeSelected() {
				console.log(this.selected);
			},
			exCode(type = undefined) {
				var strlist = '0123456789ABCDEFGHJKLMNPQRSTUVWXYZ'
				this.output = 'AVS2WTX4T5G'
				// 10	34
				// 2Y	100
				// VE	1000
				// ZZ	1155
				// 间隔 7

				// 始数 D34 间隔
				// [34,'10',7],
				// [35,'11',21],
				// [36,'12',14],
				// [37,'13',129],

				// 资源序号		34
				// var resnum = [
				// 	'10', '17', '1E', '1M', '1U', '21', '28', '2F', '2N', '2V', '32', '39', '3G', '3P', '3W', '43',
				// 	'4A', '4H', '4Q', '4X', '54', '5B', '5J', '5R', '5Y', '65', '6C', '6K', '6S', '6Z', '76', '7D',
				// 	'7L', '7T', '80', '87', '8E', '8M', '8U', '91', '98', '9F', '9N', '9V', 'A2', 'A9', 'AG', 'AP',
				// 	'AW', 'B3', 'BA', 'BH', 'BQ', 'BX', 'C4', 'CB', 'CJ', 'CR', 'CY', 'D5', 'DC', 'DK', 'DS', 'DZ',
				// 	'E6', 'ED', 'EL', 'ET', 'F0', 'F7', 'FE', 'FM', 'FU', 'G1', 'G8', 'GF', 'GN', 'GV', 'H2', 'H9',
				// 	'HG', 'HP', 'HW', 'J3', 'JA', 'JH', 'JQ', 'JX', 'K4', 'KB', 'KJ', 'KR', 'KY', 'L5', 'LC', 'LK',
				// 	'LS', 'LZ', 'M6', 'MD', 'ML', 'MT', 'N0', 'N7', 'NE', 'NM', 'NU', 'P1', 'P8', 'PF', 'PN', 'PV',
				// 	'Q2', 'Q9', 'QG', 'QP', 'QW', 'R3', 'RA', 'RH', 'RQ', 'RX', 'S4', 'SB', 'SJ', 'SR', 'SY', 'T5',
				// 	'TC', 'TK', 'TS', 'TZ', 'U6', 'UD', 'UL', 'UT', 'V0', 'V7', 'VE', 'VM', 'VU', 'W1', 'W8', 'WF',
				// 	'WN', 'WV', 'X2', 'X9', 'XG', 'XP', 'XW', 'Y3', 'YA', 'YH', 'YQ', 'YX', 'Z4', 'ZB', 'ZJ', 'ZR',
				// 	'ZY'
				// ]

				// 资源组序号	35
				// var resgroupnum = [
				// 	'11', '1N', '29', '2W', '3H', '44', '4R', '5C', '5Z', '6L', '77', '7U', '8F', '92', '9P', 'AA',
				// 	'AX', 'BJ', 'C5', 'CS', 'DD', 'E0', 'EM', 'F8', 'FV', 'GG', 'H3', 'HQ', 'JB', 'JY', 'KK', 'L6',
				// 	'LT', 'ME', 'N1', 'NN', 'P9', 'PW', 'QH', 'R4', 'RR', 'SC', 'SZ', 'TL', 'U7', 'UU', 'VF', 'W2',
				// 	'WP', 'XA', 'XX', 'YJ', 'Z5', 'ZS'
				// ]

				// 数量序号		36
				// var countnum = [
				// 	'12', '1G', '1W', '2A', '2Q', '34', '3J', '3Y', '4C', '4S', '56', '5L', '60', '6E', '6U', '78',
				// 	'7N', '82', '8G', '8W', '9A', '9Q', 'A4', 'AJ', 'AY', 'BC', 'BS', 'C6', 'CL', 'D0', 'DE', 'DU',
				// 	'E8', 'EN', 'F2', 'FG', 'FW', 'GA', 'GQ', 'H4', 'HJ', 'HY', 'JC', 'JS', 'K6', 'KL', 'L0', 'LE',
				// 	'LU', 'M8', 'MN', 'N2', 'NG', 'NW', 'PA', 'PQ', 'Q4', 'QJ', 'QY', 'RC', 'RS', 'S6', 'SL', 'T0',
				// 	'TE', 'TU', 'U8', 'UN', 'V2', 'VG', 'VW', 'WA', 'WQ', 'X4', 'XJ', 'XY', 'YC', 'YS', 'Z6', 'ZL'
				// ]

				// 状态序号		9
				// var statenum = ['13','4T','8H','C7','FX','KM','PB','T1','WR']

				// 通过资源组序号进行查找，对应的资源组序号中可包含资源序号和数量的，让左右自定义的内容在开发部分，而不在调用部分

				// 资源列表7	1.序号，2.D34，3.物品名，4.数量
				var reslist = [
					[34, '10', '金币', '5000'],
					[41, '17', '钻石', '20'],
					[48, '1E', '角色号召令', '100'],
					[55, '1M', '铜钥匙', '50'],
					[62, '1U', '银钥匙', '50'],
					[69, '21', '金钥匙', '50'],
					[76, '28', '天赋石', '2'],
					[83, '2F', '体力', '20'],
					[90, '2N', '广告券', '2'],
				]

				// 资源组映射表21	1.序号，2.D34，3.资源，4.对应数量，5.速记名
				var resgroupmapping = [
					[35, '11', '1017', '', 'VIP666'],
					[56, '1N', '17', '4S', 'VIP888'],
					[77, '29', '1M1U21', '', 'TSSZ2023'],
					[98, '2W', '282F2N', '', '666666'],
				]

				// 数量映射表14	1.序号，2.D34，3.数量
				var countmapping = [
					[36, '12', 5],
					[50, '1G', 10],
					[64, '1W', 20],
					[78, '2A', 50],
					[92, '2Q', 66],
					[106, '34', 88],
					[120, '3J', 100],
					[134, '3Y', 120],
					[148, '4C', 180],
					[162, '4S', 200],
					[176, '56', 500],
					[190, '5L', 666],
					[204, '60', 888],
					[218, '6E', 1000],
					[232, '6U', 2000],
					[246, '78', 5000],
					[260, '7N', 6666],
					[274, '82', 8888],
					[288, '8G', 10000],
					[302, '8W', 20000]
				]

				// 状态映射表129	1.序号，2.D34，3.名称，4.参数	['13','4T','8H','C7','FX','KM','PB','T1','WR']
				var statemapping = [
					[37, '13', '已过期', '[Expired]', '1677427745'],
					[163, '4T', '已无效', '[Invalid]', '[Invalid]'],
					[289, '8H', '已兑换', '[Exchange]', '41427965'], //0X01BK转换为D34的10进制：41427965
					[415, 'C7', '已领取', '[Received]', '47890277093'], //0X0103BK转换为D34的10进制：47890277093
				]

				// 临时数据
				// let temp_select = []
				// for (var i in resgroupmapping) {
				// 	temp_select.push(resgroupmapping[i][1])
				// }

				// this.select = tempmapping

				var tempmapping = [
					[36, '12', 5],
					[50, '1G', 10],
					[64, '1W', 20],
				]

				tempmapping = tempmapping.map((item) => ({
					id: item[0],
					val: item[1]
				}))
				console.log(tempmapping);

				console.log("reslist", reslist);
				console.log("resgroupmapping", resgroupmapping);
				console.log("countmapping", countmapping);
				console.log("tempmapping", tempmapping);
				console.log("statemapping", statemapping);
				console.clear()

				const f = {
					//20190330 ==> F3PP8
					//20221228 ==> F4GE0
					en_d_34: (str = 'undefined' || 0) => {
						if (isNaN(str)) { //isFinite或者isNaN
							console.error('[Error]String contains [a-zA-z].');
							return '[Error]isNaN.'
						}
						var result = ''
						while (str) {
							result = strlist[str % 34] + result
							str = Math.floor(str / 34)
						}
						// 临时数据
						// this.output = result
						// console.log(result);
						return result
					},
					de_34_d: (str = '') => {
						str = str.toUpperCase()
						var strlen = str.length
						var result = 0
						while (strlen) {
							result += strlist.indexOf(str[strlen - 1]) * Math.pow(34, str.length - strlen)
							// console.log(str[strlen - 1], result)
							strlen--
						}
						// console.log(result);
						return result
					},
					randstr: (len = 1) => {
						var result = '';
						for (var i = 0; i < len; i++) {
							let index = Math.floor(Math.random() * strlist.length);
							result += strlist[index];
						}
						return result
					},
					generate: (rescode) => {
						// 规则：2位字符表示序号，2位字符表示数量(00-0Z为特殊数量，10开始)
						// *类型状态：有效时间，领取次数(过期，失效，已兑换)，速记名称
						// 前3位随机码，后2位随机码，最后2位校验和算法
						// 乱码排序[8, 1, 5, 9, 2, 4, 0, 12, 7, 10, 13, 11, 3, 6]
						// 参数：rescode	资源码

						// 预处理
						if (rescode == undefined) rescode = ''
						// console.log(rescode);

						// 开头随机码
						var result = f.randstr(3)
						console.log(result, "开头");

						// 加入资源组序列：rescode
						rescode = '2W'
						result += rescode

						// 中段随机码
						result += f.randstr(2)
						console.log("result", result);

						// 加入状态码
						var statecode = '4T'
						result += statecode

						// 校验码
						let cnt = 0;
						for (var j in result) {
							cnt += strlist.indexOf(result[j])
						}
						result += cnt < 34 ? '0' + f.en_d_34(cnt) : f.en_d_34(cnt)
						console.log(cnt, f.en_d_34(cnt), result);

						this.output = result
						return result
					},
					disorder: (str) => {
						// 乱码排序(A-1规则)
						// 原始一组[1 2 3 4 5 6 7 8 9]
						// 一组规则[A B B 1 2 2 1 2 2],[C B A 2 2 1 C B B]
						// 一组资源[9 1 5 8 2 4 7 3 6],[6 9 5 1 7 4 2 8 3]
						// 
						// 原始两组[1 2 3 4 5 6 7 A B C D 8 9]
						// 两组规则[B C C 2 3 3 2 A B B C 2 4],[B C D 2 3 4 2 3 3 3 B B C],[A C D 2 4 3 2 4 6 2 E D 3]
						// 两组资源[9 B 1 5 C 8 2 4 D 7 3 A 6],[D B 1 8 5 A 2 9 4 7 C 3 6],[9 1 5 A 8 2 C 4 7 B 3 6 D]
						// 
						// 原始三组[1 2 3 4 5 6 7 A B C D M N P Q 8 9]
						// 三组规则[B B C C C 1 3 3 2 2 1 3 3 A C E B],[A B D 2 2 2 2 3 B B B D C 2 2 F B]
						// 三组资源[5 C 1 8 B 2 9 A N 3 P 7 M 4 Q 6 D],[6 1 D 5 2 Q 4 M P 3 A N 8 B 7 9 C]
						// 长度规则：4n+5

						var rule = ['ABB122122', 'CBA221CBB']
						// var rule2 = ['BCC2332ABBC24', 'BCD2342333BBC', 'ACD2432462ED3']
						// var rule = ['BBCCC13322133ACEB', 'ABD22223BBBDC22FB']

						var result = str

						// result 传入参数
						// var res = 'KCE10VE3Y'
						// result = '1234567ABCDMNPQ89'
						var exres = new Array(result.length).fill('0')
						console.log(rule, exres.length);
						var randlen = Math.floor(Math.random() * rule.length)
						var [strcode, distance, distlist] = ['', 0, []] //字符编码,字符距离,序列表
						for (var k in rule[randlen]) {
							// 获取每个字符距离首字符的距离，匹配A-1规则	48-0	65-A
							strcode = rule[randlen][k].charCodeAt()
							if (strcode > 48 && strcode <= 57) {
								// 数字为负值
								distance = distance - (strcode - 48) - 1 <= 0 ?
									distance - (strcode - 48) + rule[randlen].length - 1 :
									distance - (strcode - 48) - 1
							} else if (strcode > 64 && strcode <= 90) {
								// 字母为正值
								distance = distance + (strcode - 64) + 1 > rule[randlen].length ?
									distance + (strcode - 64) - rule[randlen].length + 1 :
									distance + (strcode - 64) + 1
							}
							distlist.push(distance - 1) // 规则转序号数组
							// console.log(String.fromCharCode(strcode), distance - 1, parseInt(k) + 1);
						}
						// console.log("distlist", distlist, "res", res);
						// 按照序号数组重新分配兑换码
						for (var m in result) {
							exres[distlist[m]] = result[m]
						}
						exres = exres.join('')
						console.log(result);
						console.log(exres);

						// 存储
						window.localStorage.setItem('bk_GE_excode', result)
						this.output = result
						return result
					},
					reverse: (str) => {
						// 去除校验和随机码
						// str = 'K2V10VE17051E1G43'
						str = window.localStorage.getItem('bk_GE_excode') || 'vip666'
						console.log(str);
						str = str.split('')

						str.splice(0, 3)
						str.splice(-2, 2)
						console.log(str);

						// 获取资源表
						var reslen = str.length
						var reslist = []
						for (var i = 0; i < reslen; i += 2) {
							reslist.push(str.splice(0, 2).join(''))
						}
						var result = []
						for (var j in reslist) {
							if (j % 2 == 1) {
								result.push([reslist[j - 1], reslist[j]])
							}
						}
						console.log(reslist, result);

					},
					analyse: (code) => {
						// code = 'AVS2WTX4T5G'
						console.log(code);
						console.log(code.length);
						
						if (code.length!=11) {
							console.warn('[Warn]Length error.');
						}
						
						// 解析兑换码
						var [res, state, check] = [
							code.slice(3, 5),
							code.slice(7, 9),
							code.slice(9, 11)
						]
						console.log(res, '资源');
						console.log(state, '状态');
						console.log(check, '校验');

						var bk_GE_state = JSON.parse(window.localStorage.getItem('bk_GE_state')) || [];

						// 已兑换过的兑换码记录
						var codelist = bk_GE_state.map((item) => (item.code))
						console.log(codelist);

						// 状态分析
						var codejson = {
							code: code,
							state: {
								expired: new Date().getTime(),
								invalid: "",
								exchange: "",
								received: ""
							}
						}

						const analy = {
							state: () => {
								var statecode = statemapping.map((item) => (item[1]))
								console.log(statecode, "状态码", state);

								// 状态判断存在
								if (statecode.indexOf(state) != -1) { //存在此状态
									let index = statecode.indexOf(state)
									console.log(index, statemapping[index][2]);
								} else {
									console.log(statemapping[1][3]);
								}
								
								// 已经输入过兑换码
								if (codelist.indexOf(code) != -1) {
									console.log(code);
								}else{
									bk_GE_state.push(codejson)
								}
								
								// 存储数据
								console.log(bk_GE_state);
								window.localStorage.setItem('bk_GE_state', JSON.stringify(bk_GE_state));
							},
							res: () => {
								console.log(res, "资源");
							},
							verify: () => {
								console.log('校验');
								console.log('校验',check); 
							}
						}

						// 状态处理
						analy.state()
						// 资源处理
						analy.res()
						// 校验值判断
						analy.verify()
					},
					test: () => {
						var cnt = 0
						for (var i = 37; i < 1156; i += 126) {
							f.en_d_34(i)
							cnt++
						}
						console.log("总计", cnt);
					},
				}
				// f.test()

				switch (type) {
					case 1:
						this.output = f.en_d_34(this.code);
						console.log(this.output)
						break;
					case 2:
						this.output = f.de_34_d(this.code);
						console.log(this.output)
						break;
					case 3:
						f.generate()
						break;
					case 4:
						f.reverse();
						break;
					case 5:
						f.disorder(f.generate());
						break;
					case 6:
						f.state();
						break;
					case 7:
						f.analyse(this.excode);
						break;
					default:
						console.warn("[Warn]Without type.")
						break;
				}
			}
		}
	}
</script>

<style scoped>
	input {
		height: 32px;
		font-size: 20px;
		margin: 4px;
	}

	button {
		margin: 2px;
	}
</style>
