		--wrapper-padding: 5px;
		--wrapper-max-width: 390px;
		--wrapper-max-height: 750px;
	}

	:root {
		font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
		/* font-size: 16px; */
		/* line-height: 24px; */
		/* font-weight: 400; */

		color-scheme: light dark;
		/* color: rgba(255, 255, 255, 0.87); */
		/* background-color: #242424; */

		font-synthesis: none;
		text-rendering: optimizeLegibility;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		-webkit-text-size-adjust: 100%;
		-ms-text-size-adjust: 100%;
	}

	#app {
		font-family: Avenir, Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		max-width: 1280px;
		margin: 0 auto;
	}

	body {
		display: flex;
		place-items: center;
		/* background-color: #cc991177; */
	}

	.wrapper {
		background-color: #66dd9977;
		padding: var(--wrapper-padding);
		width: calc(var(--wrapper-max-width) - var(--wrapper-padding)*2);
		max-width: var(--wrapper-max-width);
		height: auto;
		min-height: 722px;
		margin: var(--wrapper-margin-top-bottom) auto;
		position: relative;
	}

	a {
		font-weight: 500;
		color: #646cff;
		text-decoration: inherit;
	}

	a:hover {
		color: #535bf2;
	}

	a:active {
		color: #42b983;
	}

	nav a.router-link-exact-active {
		color: #42b983;
	}

	h1 {
		font-size: 3.2em;
		line-height: 1.1;
	}

	button {
		border-radius: 8px;
		border: 1px solid transparent;
		padding: 0.6em 1.2em;
		font-size: 1em;
		font-weight: 500;
		font-family: inherit;
		border-color: #006e90;
		cursor: pointer;
		transition: border-color 0.25s;
	}

	button:hover {
		border-color: #646cff;
	}

	button:focus,
	button:focus-visible {
		outline: 4px auto -webkit-focus-ring-color;
	}



	@media (prefers-color-scheme: light) {
		:root {
			color: #213547;
			background-color: #ffffff;
		}

		nav a.router-link-exact-active {
			color: #42b983;
		}

		a:hover {
			color: #747bff;
		}

		button {
			background-color: #f9f9f9;
		}
	}
</style>
